.box{
    background-color: wheat;
    border: none;
    border-radius: 10%;
    box-shadow: 0px 0px 12px #888;
    width: 5rem;
    height: 5rem;
    text-align: center;
    font-size: 5em;
    font-family: math;
    line-height: 5rem;
    margin: 0.5rem;
}

.x{
    color: rgb(147, 38, 9);
}

.o{
    color: blue;
}

.box:hover{
    box-shadow: 0px 0px 15px #888;
}